                    <p>HIGHHOUR: {{ inCurrency(price).XRP.HIGHHOUR }}</p>
                    <p>LOWHOUR: {{ inCurrency(price).XRP.LOWHOUR }}</p>
                    <p>Open Day: {{ inCurrency(price).XRP.OPEN24HOUR }}</p>
                    <p>High Day: {{ inCurrency(price).XRP.HIGH24HOUR }}</p>
                    <p>Low Day: {{ inCurrency(price).XRP.LOW24HOUR }}</p>
                    <p>Last Trade Volume: {{ inCurrency(price).XRP.LASTVOLUME }}</p>
                    <p>Last Trade Volume To: {{ inCurrency(price).XRP.LASTVOLUMETO }}</p>
                    <p>24h Volume: {{ inCurrency(price).XRP.VOLUME24HOUR }}</p>
                    <p>24h VolumeTo: {{ inCurrency(price).XRP.VOLUME24HOURTO }}</p>
                </md-content>
            </div>
